"use client";

import { useState, useEffect, useRef } from "react";

export default function HomePage() {
  const [file, setFile] = useState<File | null>(null);
  const [prompt, setPrompt] = useState("");
  const [result, setResult] = useState("");
  const [tone, setTone] = useState("");
  const [loading, setLoading] = useState(false);
  const [preview, setPreview] = useState<string | null>(null);
  const [copiedIndex, setCopiedIndex] = useState<number | null>(null);
  const [authenticated, setAuthenticated] = useState(false);
  const [password, setPassword] = useState("");
  const [history, setHistory] = useState<string[]>([]);
  const [usageCount, setUsageCount] = useState(0);
  const [selectedCaption, setSelectedCaption] = useState<string | null>(null);
  const [selectedHashtags, setSelectedHashtags] = useState<string | null>(null);
  const copyButtonRef = useRef<HTMLDivElement | null>(null);
  const [showToast, setShowToast] = useState(false);

  useEffect(() => {
    const savedHistory = localStorage.getItem("postpilot_history");
    const savedUsage = localStorage.getItem("postpilot_usage");
    const today = new Date().toDateString();

    if (savedHistory) {
      setHistory(JSON.parse(savedHistory));
    }

    if (savedUsage) {
      const parsed = JSON.parse(savedUsage);
      if (parsed.date === today) {
        setUsageCount(parsed.count);
      } else {
        localStorage.setItem(
          "postpilot_usage",
          JSON.stringify({ date: today, count: 0 })
        );
        setUsageCount(0);
      }
    }
  }, []);

  useEffect(() => {
    if (selectedCaption && selectedHashtags && copyButtonRef.current) {
      copyButtonRef.current.scrollIntoView({ behavior: "smooth" });
    }
  }, [selectedCaption, selectedHashtags]);

  useEffect(() => {
    if (result) {
      const el = document.getElementById("results-container");
      if (el) {
        el.classList.remove("opacity-0");
        el.classList.add("opacity-100");
      }
    }
  }, [result]);

  if (!authenticated) {
    return (
      <main className="min-h-screen flex items-center justify-center bg-gray-100">
        <div className="bg-white p-8 rounded-xl shadow-lg w-full max-w-sm flex flex-col gap-4">
          <h2 className="text-2xl font-bold text-center">PostPilot Access</h2>
          <input
            type="password"
            placeholder="Enter access password"
            value={password}
            onChange={(e) => setPassword(e.target.value)}
            className="border p-2 rounded"
          />
          <button
            onClick={() => {
              if (password === "postpilot123") {
                setAuthenticated(true);
              } else {
                alert("Incorrect password");
              }
            }}
            className="bg-blue-600 text-white p-2 rounded hover:bg-blue-700"
          >
            Enter
          </button>
        </div>
      </main>
    );
  }

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    if (!file) return alert("Please select a file");

    if (usageCount >= 5) {
      return alert("Daily limit reached (5 generations per day). Upgrade coming soon ðŸš€");
    }

    setSelectedCaption(null);
    setSelectedHashtags(null);

    const formData = new FormData();
    formData.append("file", file);
    formData.append("prompt", prompt);
    formData.append("tone", tone);

    try {
      setLoading(true);
      const res = await fetch("http://localhost:4000/generate-caption", {
        method: "POST",
        body: formData,
      });

      if (!res.ok) {
        const text = await res.text();
        throw new Error(`Server error: ${res.status} - ${text}`);
      }

      const data = await res.json();
      const newCaption = data.caption || "No caption returned";
      setResult(newCaption);
      setLoading(false);

      const updatedHistory = [newCaption, ...history].slice(0, 10);
      setHistory(updatedHistory);
      localStorage.setItem("postpilot_history", JSON.stringify(updatedHistory));

      const today = new Date().toDateString();
      const newUsage = usageCount + 1;
      setUsageCount(newUsage);
      localStorage.setItem(
        "postpilot_usage",
        JSON.stringify({ date: today, count: newUsage })
      );
    } catch (err: any) {
      setLoading(false);
      console.error("Fetch error:", err);
      alert(err.message || "Error generating caption");
    }
  };

  return (
    <main className="min-h-screen flex flex-col items-center p-6 bg-gradient-to-br from-gray-50 to-gray-200 text-black">
      <div className="text-center mb-6">
        <h1 className="text-4xl font-bold mb-2">PostPilot ðŸš€</h1>
        <p className="text-gray-600">AI-powered captions that convert.</p>
        <p className="text-sm text-gray-500 mt-2">
          Daily Usage: {usageCount}/5
        </p>
      </div>

      <form
        className="flex flex-col gap-3 w-full max-w-lg bg-white p-6 rounded-xl shadow-lg"
        onSubmit={handleSubmit}
      >
        <input
          type="file"
          accept="image/*"
          onChange={(e) => {
            const selectedFile = e.target.files?.[0] || null;
            setFile(selectedFile);
            if (selectedFile) {
              setPreview(URL.createObjectURL(selectedFile));
            }
          }}
          className="border p-2"
        />
        {preview && (
          <img
            src={preview}
            alt="Preview"
            className="w-full h-48 object-cover rounded-lg border"
          />
        )}
        <input
          type="text"
          placeholder="Enter prompt (optional)"
          value={prompt}
          onChange={(e) => setPrompt(e.target.value)}
          className="border p-2"
        />
        <select
          value={tone}
          onChange={(e) => setTone(e.target.value)}
          className="border p-2"
        >
          <option value="">Select Tone</option>
          <option value="luxury and premium">Luxury</option>
          <option value="high-energy fitness">Fitness</option>
          <option value="Gen Z hype and trendy">Gen Z</option>
          <option value="corporate and professional">Corporate</option>
          <option value="minimalist and clean">Minimalist</option>
        </select>
        <button
          type="submit"
          disabled={loading}
          className="bg-blue-600 text-white p-3 rounded-lg font-medium hover:bg-blue-700 transition disabled:opacity-50"
        >
          {loading ? "Generating..." : "Generate Captions"}
        </button>
        <button
          type="button"
          onClick={() => {
            setFile(null);
            setPreview(null);
            setResult("");
            setPrompt("");
            setTone("");
          }}
          className="bg-gray-200 text-gray-800 p-3 rounded-lg font-medium hover:bg-gray-300 transition"
        >
          Reset
        </button>
      </form>

      {loading && (
        <div className="mt-8 w-full max-w-5xl animate-pulse">
          <div className="grid grid-cols-1 md:grid-cols-2 gap-8">
            {[...Array(3)].map((_, i) => (
              <div key={i} className="h-24 bg-gray-200 rounded-lg"></div>
            ))}
            {[...Array(3)].map((_, i) => (
              <div key={`h-${i}`} className="h-24 bg-gray-200 rounded-lg"></div>
            ))}
          </div>
        </div>
      )}

      {result && !loading && (
        <div
          id="results-container"
          className="mt-8 w-full max-w-5xl transition-opacity duration-500 opacity-0"
        >

          {(() => {
            const blocks = result
              .split("\n\n")
              .filter((block) => block.trim().length > 20)
              .slice(0, 6);

            const captions = blocks.slice(0, 3).map((block) =>
              block
                .split("\n")
                .filter((line) => !line.trim().startsWith("#"))
                .join(" ")
            );

            const hashtags = blocks.slice(3, 6).map((block) =>
              block
                .split("\n")
                .filter((line) => line.trim().startsWith("#"))
                .join(" ")
            );

            return (
              <>
                <div className="grid grid-cols-1 md:grid-cols-2 gap-8">

                  {/* CAPTIONS */}
                  <div>
                    <h3 className="text-lg font-semibold mb-4">Caption Options</h3>
                    <div className="space-y-4">
                      {captions.map((caption, idx) => (
                        <div
                          key={idx}
                          onClick={() => setSelectedCaption(caption)}
                          className={`relative p-4 border rounded-lg cursor-pointer transition transform hover:scale-[1.01] ${
                            selectedCaption === caption
                              ? "border-blue-600 bg-blue-50 scale-[1.02]"
                              : "bg-white hover:border-gray-400"
                          }`}
                        >
                          {selectedCaption === caption && (
                            <span className="absolute top-2 right-2 text-blue-600 font-bold">
                              âœ“
                            </span>
                          )}
                          {caption}
                        </div>
                      ))}
                    </div>
                  </div>

                  {/* HASHTAGS */}
                  <div>
                    <h3 className="text-lg font-semibold mb-4">Hashtag Options</h3>
                    <div className="space-y-4">
                      {hashtags.map((tag, idx) => (
                        <div
                          key={idx}
                          onClick={() => setSelectedHashtags(tag)}
                          className={`relative p-4 border rounded-lg cursor-pointer transition transform hover:scale-[1.01] ${
                            selectedHashtags === tag
                              ? "border-green-600 bg-green-50 scale-[1.02]"
                              : "bg-white hover:border-gray-400"
                          }`}
                        >
                          {selectedHashtags === tag && (
                            <span className="absolute top-2 right-2 text-green-600 font-bold">
                              âœ“
                            </span>
                          )}
                          {tag}
                        </div>
                      ))}
                    </div>
                  </div>

                </div>

                {/* COPY COMBO BUTTON */}
                {selectedCaption && selectedHashtags && (
                  <div ref={copyButtonRef} className="mt-8 text-center space-y-4">
                    <button
                      onClick={() => {
                        navigator.clipboard.writeText(
                          `${selectedCaption}\n\n${selectedHashtags}`
                        );
                        setShowToast(true);
                        setTimeout(() => setShowToast(false), 2000);
                      }}
                      className="bg-black text-white px-6 py-3 rounded-lg font-medium hover:opacity-90 transition"
                    >
                      Copy Selected Caption + Hashtags
                    </button>

                    <button
                      onClick={() => {
                        setSelectedCaption(null);
                        setSelectedHashtags(null);
                      }}
                      className="block mx-auto text-sm text-gray-500 hover:underline"
                    >
                      Clear Selection
                    </button>
                  </div>
                )}
              </>
            );
          })()}
        </div>
      )}

      {history.length > 0 && (
        <div className="mt-12 w-full max-w-lg">
          <h2 className="text-xl font-semibold mb-4">Recent Generations</h2>
          <div className="space-y-3">
            {history.map((item, idx) => (
              <div
                key={idx}
                className="p-3 bg-white border rounded-lg text-sm shadow-sm"
              >
                {item.substring(0, 150)}...
              </div>
            ))}
          </div>
        </div>
      )}
      {showToast && (
        <div className="fixed bottom-6 right-6 bg-black text-white px-5 py-3 rounded-lg shadow-lg transition-opacity">
          Copied to clipboard âœ…
        </div>
      )}
    </main>
  );
}